import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-cover-page',
  templateUrl: './cover-page.component.html',
  styleUrls: ['./cover-page.component.css']
})
export class CoverPageComponent {

  constructor() {
  }
  private motivational_phrase = "Welcome to my website!";
  private phrases = [
    "You're going to kill it today.",
    "Think outside of the box.",
    "This is your moment.",
    "The issue you are facing is not insurmountable.",
    "You are surrounded by a smart team to help you achieve great success.",
    "Dig hard, and you will find the right answer.",
    "You are the only person with your exact blend of talents and skills.",
    "Even if you fail today, you will learn.",
    "Each day is an opportunity.",
    "The sky is the limit!",
  ];



  changeMotivationalPhrase() {
    (<HTMLInputElement>document.getElementById("getMotivated")).disabled = true;
    setTimeout(() => {
      this.removeMotivationalPhrase();
      if(this.motivational_phrase != "") {
        this.changeMotivationalPhrase();
      }
      else {
        var newPhrase = this.phrases[Math.floor(Math.random()*this.phrases.length)];
        setTimeout(() => {
          this.addMotivationalPhrase(newPhrase)
        }, 1000);
      }
    }, 50);
  }

  addMotivationalPhrase(phrase) {
    var func = setTimeout(() => {
      if(phrase != "") {
        this.motivational_phrase += phrase.substring(0,1);
        var newPhrase = phrase.substring(1);
        this.addMotivationalPhrase(newPhrase);
      }
      else {
        (<HTMLInputElement>document.getElementById("getMotivated")).disabled = false;
      }
    }, 50);
  }

  removeMotivationalPhrase() {
        this.motivational_phrase = this.motivational_phrase.substring(0, this.motivational_phrase.length-1);
  }

}
